import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import cuPanel from '@/components/custom/cu-panel.vue';
import cuCell from '@/components/custom/cu-cell.vue';
import cuCellGroup from '@/components/custom/cu-cell-group.vue';
import { post, tokenpost } from '@/api/user.js';
import { api } from '@/config/common.js';
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup,
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '员工管理',
			dataList: []
		};
	},
	onLoad() {},
	onShow() {
		if (!uni.getStorageSync('userInfo')) {
			uni.reLaunch({
				url: '/pages/my/login/login'
			});
		}
		this.loadData();
	},
	methods: {
		handleAdd() {
			uni.navigateTo({
				url: '/pages/my/user/useradd'
			});
		},
		handleEdit(val) {
			uni.navigateTo({
				url: '/pages/my/user/useredit?userid=' + val
			});
		},
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			});
		},
		handleForbiddenChanage(val) {
			this.setUserLock(val.currentTarget.id, val.detail.value);
		},
		loadData() {
			tokenpost(api.GetUserList)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.dataList = res.data.data.resultList;
					} else {
						this.$api.msg(res.data.returnMessage);
					}
					this.loading = false;
				})
				.catch(error => {
					this.loading = false;
					this.$api.msg('请求失败fail');
				});
		},
		setUserLock(userid, isdelete) {
			const sendData = { userid: userid, isdelete: isdelete };
			tokenpost(api.SaveUserStatus, sendData)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.$api.msg('操作成功！');
					} else {
						this.$api.msg(res.data.returnMessage);
					}
					this.loading = false;
				})
				.catch(error => {
					this.loading = false;
					this.$api.msg('请求失败fail');
				});
		}
	}
};
